<template>
  <div>
    <b-jumbotron>
      <template slot="header">
          <icon name="pencil" scale="3.5"></icon>
          Tus Cursos
      </template>
      <template slot="lead">
        Aqu√≠ puedes ver tus cursos pendientes y terminarlos.
      </template>      
    </b-jumbotron>
    <div class="row">
      <div class="col-md-12">
        <template>
          <b-table striped hover :items="items":fields="fields">
            <template slot="actions" slot-scope="row">
              <!-- We use click.stop here to prevent a 'row-clicked' event from also happening -->
              <b-btn variant="success" size="sm" @click.stop="details(row.item,row.index,$event.target)">Cursar</b-btn>
            </template>
          </b-table>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
const items = [
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'}
];

export default {
  data() {
    return {
    items: items,
    fields: {
      curso:        { label: 'Nombre del Curso', sortable: true },
      FechaLimite:  { label: 'Fecha Limite', sortable: true, 'class': 'text-center'  },      
      actions:      { label: 'Cursar' , sortable: false,}
    },
  }
  }
}
</script>


