<template>
	<div class="container">
		<br>
	    <div class="row">
	      <div class="col-md-12">
	        <h1>{{NombreCurso}}</h1>
	      </div>
	    </div>
	    <br>
     	<h2>Usuarios</h2>
		<div class="row">
			<div class="col-md-12">
				<template>
					<b-table striped hover :items="items":fields="fields">
					  <template slot="actions" slot-scope="row">
					    <!-- We use click.stop here to prevent a 'row-clicked' event from also happening -->
					    <b-btn variant="success" size="sm" >Agregar</b-btn>
					  </template>
					</b-table>
				</template>
			</div>
		</div>
		<br>
		<h2>Usuarios inscritos</h2>
		<div class="row">
			<div class="col-md-12">
				<template>
				<b-table striped hover :items="UsrCurso":fields="campos">
				  <template slot="actions" slot-scope="row">
				    <!-- We use click.stop here to prevent a 'row-clicked' event from also happening -->
				    <b-btn variant="danger" size="sm" >Eliminado</b-btn>
				  </template>
				</b-table>
				</template>
			</div>
		</div>

	</div>
</template>

<script>
const items = [
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'}
];
const UsrCurso = [
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'}
];
export default {
  data() {
    return {
		NombreCurso: "Curso1",
		items: items,
		fields: {
			curso:        { label: 'Usuario', sortable: true },	            
			actions:      { label: 'Agregar' , sortable: false,}
	    },
		UsrCurso:UsrCurso,
		campos: {
			curso:        { label: 'Usuario', sortable: true },	            
			actions:      { label: 'Eliminar' , sortable: false,}
	    },
    }
  }
}
</script>


