<template>
  <div>
    <b-jumbotron>
      <template slot="header">
          <icon name="calendar-plus-o" scale="3.5"></icon>
          Alta de Curso
      </template>
      <template slot="lead">
        Aqu√≠ puedes administrar los cursos.
      </template>      
    </b-jumbotron> 
    <div class="container">
      <b-button variant="success">Agregar curso</b-button>
      <div class="row">
        <div class="col-md-12">
          <template>
            <b-table striped hover :items="items":fields="fields">
              <template slot="actions" slot-scope="row">
                <!-- We use click.stop here to prevent a 'row-clicked' event from also happening -->
                <b-btn variant="info" size="sm" >Participantes</b-btn>
              </template>
              <template slot="borrar" slot-scope="row">
                <!-- We use click.stop here to prevent a 'row-clicked' event from also happening -->
                <b-btn variant="danger" size="sm">Eliminar</b-btn>
              </template>
            </b-table>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const items = [
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'},
  { curso: 'curso1',FechaLimite: '2016-12-11'}
];

export default {
   data () {
    return {
      items: items,
    fields: {
      curso:        { label: 'Nombre del Curso', sortable: true },
      FechaLimite:  { label: 'Fecha Limite', sortable: true, 'class': 'text-center'  },      
      actions:      { label: 'Admin Participantes' , sortable: false,},
      borrar:       { label: 'Borrar' , sortable: false,}
    },
    }
  },
}
</script>


